import React from 'react';
import ReactDOM from 'react-dom/client';
import '../../index.css';
import "./../main.css";


export default function Comp32(props) {
    return (
        <div style={{ display: "flex" }}>
            <img style={{ width: "20%", position: "fixed", bottom: "25px", right: "25px" }} src="https://raw.githubusercontent.com/AhmetErenLacinbala/modulasyon_teknikleri/master/src/comps/comp32/pcm.jpg" alt="" />
            <div style={{ flexDirection: "column", backgroundColor: "rgba(11,11,11,0.5)" }} className="comp compFullSize compContent">
                <div className='compContent' style={{ width: "80%", }}>

                    <p style={{ marginTop: "50px", marginLeft: "50px", marginRight: "50px", marginBottom: "10px" }} className="openSansFont fontW700 fontS72">PCM Verici</p>
                    <p style={{ marginLeft: "50px" }} className=' fontS32 fontW300 openSansFont'><span className='fontW700'> LPF:</span> Burada srekli zaman formundaki mesaj sinyalinin bir alak geiren filtreden (LPF) gemesine izin verilir. Kesme frekans fm
                        olan bu LPF, sinyalin yksek frekansl bileenlerini ortadan kaldrr ve yalnzca fm'in altnda kalan frekans bileenlerini geer.</p>
                    <p style={{ marginLeft: "50px" }} className=' fontS32 fontW300 openSansFont'><span className='fontW700'> rnekleyici:</span> LPF'nin k daha sonra analog giri sinyalinin dzenli aralklarla rneklendii bir rnekleyiciye beslenir.
                        Sinyalin rneklenmesi fs hznda yaplr. <br />
                        Bu rnekleme frekans u ekilde ifade edilen rnekleme teoremini takip etmesi gerekir:</p>
                    <p style={{ textAlign: "center" }} className="openSansFont fontW700 fontS72">fs  2fm</p>
                    <p style={{ marginLeft: "50px" }} className=' fontS32 fontW300 openSansFont'>rnekleyicinin k, bir PAM sinyalinden baka bir ey olmayan nTs olarak gsterilen ayrk zamanl
                        srekli genlik sinyali olan bir sinyaldir.</p>
                </div>



            </div>
        </div>
    );
}




