import React from 'react';
import ReactDOM from 'react-dom/client';
import '../../index.css';
import "./../main.css";
import BgText from "../bgText/bgText";



export default function Comp14(props) {
    return (
        <div className="comp">
            <div className="openSansFont fontW700 compContent2">
                <p className='fontS72'>Darbe Genlik Modlasyonu (PAM)</p>
                <p className='fontW400 fontS40'>Darbe modlasyonlarnda tayc sabit genlikli ve sabit sreli (sabit genilikli) darbelerdir. Bilgi sinyali darbeleri
modle eder, yani darbelerde deiiklik yapar . Darbe genlik modlasyonunda darbelerin genlii bilgi sinyali
genliine bal olarak deiir.</p>
<p className='fontW400 fontS40'><span className='fontW700'>Dz st darbe genlik modlasyonu</span> ve <span className='fontW700'>doal darbe genlik modlasyonu </span> olarak iki tr PAM vardr.</p>
            </div>
            <BgText text="Darbe Genlik" bottom="-40px" left="-70px" />
        </div>
    );
}




