import React from 'react';
import ReactDOM from 'react-dom/client';
import '../../index.css';
import "./../main.css";
import BgText from "./../bgText/bgText";



export default function Comp2(props) {
    return (
        <div className="comp">
            <div className="openSansFont fontW700 compContent">
                <p className='fontS48'>Modlasyona neden ihtiya duyuyoruz?</p>
                <p className='fontW400 fontS40'>Dk frekansl sinyaller yeterince enerji tamadklar iin uzak mesafelere iletilemezler. Dk
                    frekanstaki sinyalleri iletebilmek iin yksek frekansl sinyaller arac olarak kullanlrlar. Arac olarak
                    kullanlan bu sinyallere tayc sinyal ismi verilir. Elimizdeki bilgiyi-sinyali uzak mesafelere iletmek
                    amacyla tayc sinyalin frekans, faz ve ya genlii zerinde modlasyon vastasyla deiiklikler
                    yaparz.</p>
            </div>
        </div>
    );
}




